@import url(https://fonts.googleapis.com/css2?family=East+Sea+Dokdo&family=PT+Sans+Caption:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);
/*acá las fuentes que usé: 
font-family: 'East Sea Dokdo', cursive;
font-family: 'PT Sans Caption', sans-serif; */
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
 :root{
   --white: #ffffff;
   --black: #000000;
 }
body{
  font-family: 'PT Sans Caption', sans-serif;
  background: #edecee;
}
::-webkit-input-placeholder {
  /* Edge */
  color: #8e8e8e;
  font-weight: 300;
  font-size: 12px;
}

:-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  color: #8e8e8e;
  font-weight: 300;
  font-size: 12px;
}

::-ms-input-placeholder {
  color: #8e8e8e;
  font-weight: 300;
  font-size: 12px;
}

::placeholder {
  color: #8e8e8e;
  font-weight: 300;
  font-size: 12px;
}
.container {
  width: 100%;
  padding: 0 200px;
  margin-top: 80px;
}
@media (max-width: 765px){
  .container {
    padding: 0 20px;
  }
}


.navbar{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 54px; /*DPR*/
    background: var(--white);/*DPR*/
    display: flex;
    padding: 0 200px;
    border-bottom: 3px solid #efefef;
    align-items: center; 
}

.navbar__first{
    width: 33%;
}

.nav__logo{
    width: 50px;
    padding-top: .3rem;
}

.navbar__middle{
    width: 33%;
    display: flex;
    justify-content: center;
    
}
/*--------------------------------*/
 
.navbar__middle h1 {
  font-family: 'Pacifico', cursive;
  color: rgb(60, 127, 216);

 }
  .navbar__last {
    width: 33%;
    display: flex;
    justify-content: flex-end;
  }
  .navbar__last li {
    list-style-type: none;
    display: inline-block;
    margin: 0 10px;
    cursor: pointer;
  }
  .navbar__icons {
    font-size: 25px;
    cursor: pointer;
  }
  @media (max-width: 1200px){
      
  /*--------------------------------*/
   
  .navbar__middle h1 {
    font-size: 2.5vw;
  
   }
   .navbar__last {
    width: 50%;
    display: flex;
    justify-content: flex-end;
  }
  
   
  }
  @media (max-width: 765px){
    .navbar {
      padding: 0 20px;
    }
    .navbar__middle {
      display: none;
    }
    .navbar__icons {
      display: none;
    }

  }

.model {
    width: 100%;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.7);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .model__container {
    width: 400px;
    height: auto;
    padding: 15px;
    border-radius: 4px;
    background: var(--white);
  }
  .model__group {
    margin: 10px 0;
  }
  .model__input {
    width: 100%;
    height: 40px;
    padding: 0 12px;
    border: 1px solid #d2dbdb;
    outline: none;
    border-radius: 3px;
  }
  .form__logo{
      width: 350px;
      padding-left:18px;
  }
  .btn {
    outline: none;
    border: none;
    color: #fff;
    height: 40px;
    padding: 0 13px;
    border-radius: 3px;
    cursor: pointer;
  }
  .btn-smart {
    width: 100%;
    background: #3ad2f8;
  }
  .model__group span {
    font-size: 13px;
    cursor: pointer;
  }
  
.stories {
    display: flex;
    background: var(--white);
    padding: 15px 10px;
    width: 65%;
    border-radius: 3px;
    border: 1px solid #efefef;
    overflow: hidden;
  }
  .stories__info {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 6px;
    cursor: pointer;
  }
  .stories__img {
    width: 66px;
    height: 66px;
    border-radius: 50%;
    overflow: hidden;
    border: 2px solid #f83a63;
    padding: 3px;
  }
  .stories__img span {
    display: block;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    overflow: hidden;
  }
  .stories__img span img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  .stories__name {
    font-size: 11px;
    margin-top: 4px;
  }
  @media (max-width: 765px){
    .stories {
      width: 90%;
      margin: 0 auto;
    }
  }
.create__inputt {
    width: 65%;
    height: 40px;
    padding: 0 13px;
    border: 1px solid #efefef;
    outline: none;
    border-radius: 2px;
  }
  .create__second {
    display: flex;
    align-items: center;
    margin-top: 10px;
  }
  .file {
    display: none;
  }
  .camera {
    color: #3a7df8;
    font-size: 30px;
    cursor: pointer;
  }
  .create__second-a {
    flex: 1 1;
  }
  .create__second-b {
    flex: 1 1;
    text-align: right;
  }
  .btn-sweet {
    background: #3aa3f8;
    color: var(--white);
    text-decoration: none;
    border: none;
    padding: 6px 10px;
    margin-right: 70%;
    border-radius: 2px;
    cursor: pointer;
  }
  @media (max-width: 765px){
    .create {
      width: 90%;
      margin: 15px auto;
    }
  }
.comments {
    margin-top: 10px;
  }
  .comments__section{
    width: 100%;
  }

  .comment__input {
    width: 100%;
    height: 40px;
    border-top: 1px solid #efefef;
    border-bottom: 1px solid #efefef;
    border-left: none;
    border-right: none;
    padding: 0 15px;
    outline: none;
  }
  .comments__container {
    display: flex;
    width: 100%;
    align-items: center;
    padding: 0 10px 10px 10px;
  }
  .comments__container-name {
    margin-right: 8px;
    text-transform: capitalize;
  }
  .comments__container-msg {
    font-size: 14px;
    color: rgb(163, 153, 153);
    font-weight: 300;
  }
.posts {
    width: 65%;
    background: var(--white);
    margin: 15px 0;
  }
  .posts__header {
    display: flex;
    align-items: center;
    background: var(--white);
    padding: 10px;
  }
  
  .posts__header-avator {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    color: var(--white);
    display: flex;
    align-items: center;
    justify-content: center;
    background: #f83a63;
    text-transform: uppercase;
  }
  .posts__header-name {
    margin-left: 8px;
    text-transform: capitalize;
  }
  .posts__title{
    padding: .5rem;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif ;
    font-size: 1.2rem;
  }
  .posts__img {
    width: 100%;
    overflow: hidden;
  }
  .posts__img img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
.navbar__search{
    background: #fafafa;
    border:1px solid #d2dbdb ;
    padding: 7px 25px;
    border-radius: 3px;
    outline: none;
    font-size: 16px;
    font-weight: 400;

}



  @media (max-width: 765px){
    .posts {
      margin: 15px auto;
      width: 90%;
    }
    .navbar__search{
     
      margin-left: 30px;
  
  }
  }
  
.sidebar {
    position: fixed;
    top: 100px;
    right: 200px;
    width: 25%;
    padding-left: 25px;
        
  }
  .sidebar__user {
    display: flex;
    align-items: center;
    justify-content: space-between;
        
  }
  .sidebar__user-avator {
    width: 60px;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 18px;
    color: var(--white);
    background: #3aacf8;
    border-radius: 50%;
    text-transform: uppercase;
  }
  .sidebar__user-name {
    padding-left: 10px;
    text-transform: capitalize;
  }
  .sidebar__list {
    margin: 20px 0;
  }
  .sidebar__list h3 {
    font-size: 15px;
    font-weight: 400;
    color: silver;
  }
  .sidebar__list-user {
    display: flex;
    width: 100%;
    align-items: center;
    margin-top: 10px;
  }
  .sidebar__list-a {
    flex: 1 1;
    display: flex;
    align-items: center;
  }
  .sidebar__list-a-img {
    width: 40px;
    height: 40px;
    overflow: hidden;
    border-radius: 50%;
  }
  .sidebar__list-a-img img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  .sidebar__list-a-name {
    margin-left: 8px;
    font-size: 14px;
  }
  .sidebar__list-b {
    flex: 1 1;
    text-align: right;
  }
  .sidebar__list-b a {
    text-decoration: none;
    color: #098bbe;
    font-size: 14px;
  }
  
  @media (max-width: 1200px){
    .sidebar {
      top: 150px;
      right: 100px;
      width: 28%;
      /* padding-left: 25px; */
          
    }
    
    .sidebar__user-avator {
      width: 50px;
      height: 50px;
      font-size: 18px;
      /* border-radius: 50%; */
      
    }
    .sidebar__user-name {
      padding-left: 6px;
      
    }
    /* .sidebar__list {
      margin: 20px 0;
    } */
     .sidebar__user h3{
       font-size: 1.3vw;
     }
       
    
    .sidebar__list-a-img {
     
      border-radius: 60%;
    }
    
    .sidebar__list-a-name {
      margin-left: 20px;
      font-size: 12px;
    }
    
    .sidebar__list-b a {
      text-decoration: none;
      color: #098bbe;
      font-size: 12px;
    }
  }

  @media (max-width: 765px){
    .sidebar {
        display: none;
      }
  }
